  public showPosition(position) {
    let location = new LatLng(position.coords.latitude, position.coords.longitude);
    //this.map.panTo(location);
    //this.map.setZoom(80);
    this.marker.addMarker({
      position: location,
      //map: this.map,
      title: 'Got you!',
      icon: {
        url: "assets/imgs/people.png",
        size:{
          width:32,
          height: 32
        } // pixels
      }
    });
    this.sendLocation(location);
  }

  public sendLocation(location)
  {
    var jsonArr: any = {};
    jsonArr.location = location;
    this.http.post("/point/add/"+this.area, jsonArr).subscribe
    (
      (data) =>
      {
        var jsonResp = JSON.parse(data.text());
        if(jsonResp.coin)
        {
          alert("Yay, you got a coin!");
        }
        else
        {
          alert("Awww, you did not get a coin!");
        }
      },
      (error) =>
      {
        alert("Error: "+error);
      }
    );
  }

